import React, { useEffect, useState } from "react";
import { View, StyleSheet, Image } from "react-native";
import theme from "../../theme";
import { LinearGradient } from "expo-linear-gradient";
import BepetLogoNegativo from "../../assets/bepet-logo-negativo";
import { usePets } from "../../hooks/usePets";
import { useFocusEffect } from "@react-navigation/native";

interface LoadSplashProps {
  navigation: any;
}

export const LoadSplash: React.FC<LoadSplashProps> = ({ navigation }) => {
  const { pets, loadingPets } = usePets();

  useFocusEffect(
    React.useCallback(() => {
      if (!loadingPets) {
        console.log(pets);
        if (pets.length > 0) {
          navigation.navigate("AppStack");
        } else {
          navigation.navigate("CreatePet");
        }
      }
    }, [loadingPets])
  );
  return (
    <LinearGradient
      colors={[theme.palette.primaryOrange, theme.palette.secondaryOrange]}
      style={styles.container}
    >
      <BepetLogoNegativo />
    </LinearGradient>
  );
};

const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: theme.palette.orangeGradient,
    alignItems: "center",
    justifyContent: "center",
  },
  photo: {
    width: 200,
    height: 200,
    resizeMode: "contain",
    alignSelf: "center",
    marginTop: 200,
  },
});
