import React, {
  createContext,
  ReactNode,
  useContext,
  useEffect,
  useState,
} from "react";
import { Pet } from "../types";
import api from "../services/api";
import { useAuth } from "./useAuth";
interface PetsProviderProps {
  children: ReactNode;
}

interface PetsContextData {
  pets: Pet[];
  loadingPets: boolean;
  getPets: () => Promise<unknown>;
}

const PetsContext = createContext({} as PetsContextData);

function PetsProvider({ children }: PetsProviderProps) {
  const [pets, setPets] = useState<Pet[]>([]);
  const { logOut } = useAuth();
  const [loadingPets, setLoadingPets] = useState(true);
  function getPets() {
    return new Promise((resolve, reject) => {
    api
      .get("/v1/pets/")
      .then((response) => {
        setPets(response.data);
        resolve(response.data);
      })
      .catch((error) => {
        if (error.response.status === 401) {
          logOut();
        }
        reject(error.response);
      })
      .finally(() => {
        setLoadingPets(false);
      });
    });
  };

  useEffect(() => {
    setTimeout(() => {
      getPets();
    }, 1000);
  }, []);
  return (
    <PetsContext.Provider
      value={{
        pets,
        loadingPets,
        getPets,
      }}
    >
      {children}
    </PetsContext.Provider>
  );
}

function usePets() {
  const context = useContext(PetsContext);
  return context;
}

export { PetsProvider, usePets };
