import { useAuth } from "../../hooks/useAuth";
interface Props {
  navigation: any;
}

export const ServiceDateSelect: React.FC<Props> = ({ navigation }) => {
  const [modalVisible, setModalVisible] = React.useState(false);
  const [currentMonth, setCurrentMonth] = useState(moment());
  const [selectedDate, setSelectedDate] = useState("");
  const daysInMonth = currentMonth.daysInMonth();
  const [isEnabled, setIsEnabled] = useState(false);
  const [selectedTime, setSelectedTime] = useState("");
  const [times, setTimes] = useState<string[]>([]);
  const firstDayOfMonth = moment(currentMonth).startOf("month").format("d");
  const { getMoment } = MomentService;
  const { selectedService, setSelectedDate: setDate  } = useService();
  const [days, setDays] = useState<string[]>([]);
  const { logOut } = useAuth();
  const getTimes = (date: string) => {
    setSelectedDate(date);
    api
      .get(
        `/v1/services/provider/times?provider_id=${selectedService?.providers[0].id}&date=${date}&service_id=${selectedService?.id}`
      )
      .then((response) => {
        setTimes(response.data);
      })
      .catch((error) => {})
      .finally(() => {});
  };
  useEffect(() => {
    api
      .get(
        `/v1/services/provider/dates?provider_id=${selectedService?.providers[0].id}`
      )
      .then((response) => {
        setDays(response.data);
      })
      .catch((error) => {
        console.log(error.response.data);
        if (error.response.status === 401) {
          logOut();
        }
      });
  }, []);
  return (
    <View style={styles.container}>
      <StatusBar style={Platform.OS === "ios" ? "light" : "auto"} />
      <ScreenFlowHeader
        title="Selecione um horário"
        buttonAction={() => navigation.goBack()}
      />
      <FlatList
        style={styles.calendarList}
        data={days.map((day) => getMoment(day))}
        renderItem={({ item }) => (
          <View style={styles.calendarContainer}>
            <Text style={styles.calendarMonth}>
              {item.format("D") === "1" && item.format("MMMM")}
            </Text>
            <TouchableOpacity
              style={[
                styles.calendarItem,
                {
                  borderColor:
                    item.format("YYYY-MM-DD") === selectedDate
                      ? theme.palette.primaryOrange
                      : theme.palette.gray3,
                },
              ]}
              onPress={() => getTimes(item.format("YYYY-MM-DD"))}
            >
              <Text style={styles.calendarWeek}>
                {item.format("d/MM/YYYY") === getMoment().format("d/MM/YYYY")
                  ? "Hoje"
                  : item.format("ddd.")}
              </Text>
              <Text
                style={[
                  styles.calendarDay,
                  {
                    color:
                      item.format("YYYY-MM-DD") === selectedDate
                        ? theme.palette.primaryOrange
                        : theme.palette.black,
                  },
                ]}
              >
                {item.format("DD")}
              </Text>
            </TouchableOpacity>
          </View>
        )}
        horizontal={true}
        keyExtractor={(item) => item.toString()}
      />
      <Text style={styles.scheduleTitle}>Horário</Text>
      <ScrollView>
        {selectedDate !== "" ? (
          times.length > 0 ? (
            times.map((time) => (
              <TouchableOpacity
                style={styles.scheduleItem}
                key={time}
                onPress={() => setSelectedTime(time)}
              >
                <Text style={styles.scheduleItemText}>
                  {time} -{" "}
                  {moment(time, "HH:mm")
                    .add(selectedService?.duration, "minutes")
                    .format("HH:mm")}
                </Text>
                <View
                  style={[
                    styles.scheduleItemIcon,
                    {
                      backgroundColor:
                        time === selectedTime
                          ? theme.palette.primaryOrange
                          : theme.palette.gray3,
                    },
                  ]}
                >
                  {time === selectedTime && (
                    <View style={styles.itemTimeSelected}></View>
                  )}
                </View>
              </TouchableOpacity>
            ))
          ) : (
            <Text style={styles.scheduleItemNoTimes}>
              Nenhum horário disponível
            </Text>
          )
        ) : (
          <Text style={styles.scheduleItemNoTimes}>Selecione uma data</Text>
        )}
      </ScrollView>

      <ButtonFooter
        disabled={selectedTime === ""}
        buttonText="Confirmar"
        buttonAction={() => {
          setDate(selectedDate + " " + selectedTime);
          navigation.goBack();
        }}
      ></ButtonFooter>
    </View>
  );
};

