import React, {
  createContext,
  ReactNode,
  useContext,
  useEffect,
  useRef,
  useState,
} from "react";
import api from "../services/api";
import AsyncStorage from "@react-native-async-storage/async-storage";

interface AuthProviderProps {
  children: ReactNode;
}
interface User {
  first_name: string;
  last_name: string;
  email: string;
  phone: string;
  id: string;
  my_referral_code: string;
  indicated_by_id: string | null;
  is_active: boolean;
  created_at: string;
  updated_at: string;
  is_active_email: boolean;
  is_active_phone: boolean;
  cpf?: string | null;
  cnpj?: string | null;
  username: string;
  photo?: string;
}

interface AuthContextData {
  user: User | undefined;
  loadingAuth: boolean;
  logOut: () => void;
  createAccount: (userCreate: userCreate) => void;
  loginEmail: (password: string) => void;
  setEmailLogin: (email: string) => void;
  getUser: () => Promise<User>;
  loginEmailError: string;
  setLoginEmailError: (error: string) => void;
  createAccountError: string;
  setCreateAccountError: (error: string) => void;
  createAccountLoading: boolean;
}
interface userCreate {
  first_name: string;
  last_name: string;
  email: string;
  phone: string;
  password: string;
  indicated_code: string;
}

const AuthContext = createContext({} as AuthContextData);

function AuthProvider({ children }: AuthProviderProps) {
  const [user, setUser] = useState<User>();
  const [emailLogin, setEmailLogin] = useState<string>("");
  const [loadingAuth, setLoadingAuth] = useState(false);
  const [loginEmailError, setLoginEmailError] = useState("");
  const [createAccountLoading, setCreateAccountLoading] = useState(false);
  const [createAccountError, setCreateAccountError] = useState("");
  const hasUpdate = useRef(false);
  function logOut() {
    setUser(undefined);
    AsyncStorage.removeItem("@bepet-user-data");
  }
  const storeLoginData = async (value: {
    user: User;
    refresh_token: string;
    access_token: string;
  }) => {
    try {
      await AsyncStorage.setItem("@bepet-user-data", JSON.stringify(value));
    } catch (e) {
      // saving error
    }
  };
  async function loadStorageData() {
    const storageUser = await AsyncStorage.getItem("@bepet-user-data");
    if (storageUser) {
      const userLogged = JSON.parse(storageUser);
      setUser(userLogged.user);
    }
  }

  async function getUser() {
    return new Promise<User>((resolve, reject) => {
      api
        .get("/v1/users/")
        .then((response) => {
          setUser(response.data);
          resolve(response.data as User);
        })
        .catch((error) => {
          reject(error);
          if (error.response.status === 401) {
            logOut();
          }
        });
    });
  }
  useEffect(() => {
    if (!hasUpdate.current) {
      loadStorageData();
      hasUpdate.current = true;
    }
  }, []);

  function createAccount(userCreate: userCreate) {
    setCreateAccountError("");
    setCreateAccountLoading(true);
    api
      .post("v1/users/", userCreate)
      .then((response) => {
        setUser(response.data.user);
        storeLoginData(response.data);
      })
      .catch((error) => {
        setCreateAccountError(error.response.data.detail);
        if (error.response.status === 409) {
          setCreateAccountError("Email já cadastrado!");
        }
      }).finally(()=>{
        setCreateAccountLoading(false);
      });
  }

  function loginEmail(password: string) {
    setLoadingAuth(true);
    setLoginEmailError("");
    api
      .post("/v1/users/login", {
        email: emailLogin,
        password: password,
      })
      .then((response) => {
        setUser(response.data.user);
        storeLoginData(response.data);
      })
      .catch((error) => {
        console.log(error);
        setLoginEmailError(error.response.data.detail);
        if (error.response.status === 401) {
          setLoginEmailError("Email ou senha inválidos!");
        }
        if (error.response.status === 404) {
          setLoginEmailError("Email inxistente, por favor crie uma conta!");
        }
      })
      .finally(() => {
        setLoadingAuth(false);
      });
  }

  return (
    <AuthContext.Provider
      value={{
        user,
        logOut,
        loadingAuth,
        createAccount,
        loginEmail,
        setEmailLogin,
        getUser,
        loginEmailError,
        setLoginEmailError,
        createAccountError,
        setCreateAccountError,
        createAccountLoading,
      }}
    >
      {children}
    </AuthContext.Provider>
  );
}

function useAuth() {
  const context = useContext(AuthContext);
  return context;
}

export { AuthProvider, useAuth };
