import React, {
  createContext,
  ReactNode,
  useContext,
  useEffect,
  useState,
} from "react";
import { Service } from "../types";
import api from "../services/api";
import { useAuth } from "./useAuth";
interface ServiceProviderProps {
  children: ReactNode;
}

interface ServiceContextData {
  getServices: () => void;
  services: {Service:Service}[];
  loadingServices: boolean;
  chooseService: (serviceId: string) => void;
  selectedService?: Service;
  selectedDate: string;
  setSelectedDate: (date: string) => void;
  cancelService: (next: () => void) => void;
}

const ServiceContext = createContext({} as ServiceContextData);

function ServiceProvider({ children }: ServiceProviderProps) {
  const {logOut} = useAuth();

  const [services, setServices] = useState<{Service:Service}[]>([]);
  const [selectedService, setSelectedService] = useState<Service>();
  const [loadingServices, setLoadingServices] = useState(true);
  const [selectedDate, setSelectedDate] = useState("");
  function chooseService(serviceId: string) {
    const service = services.find((service) => service.Service.id === serviceId);
    if (service) {
      setSelectedService(service.Service);
    }
  }
  function getServices() {
    setLoadingServices(true);
    api.get("v1/services/all/services").then((response) => {
      setServices(response.data);
    }).catch((error) => {
      console.log(error.response);
      if (error.response.status === 401) {
        logOut();
      }
    }).finally(() => {
      setLoadingServices(false);
    });
  }

  function cancelService(next: () => void) {
    setSelectedService(undefined);
    setSelectedDate("");
    next && next();
  }

  return (
    <ServiceContext.Provider
      value={{
        chooseService,
        getServices,
        services,
        loadingServices,
        selectedService,
        cancelService,
        selectedDate,
        setSelectedDate,
      }}
    >
      {children}
    </ServiceContext.Provider>
  );
}

function useService() {
  const context = useContext(ServiceContext);
  return context;
}

export { ServiceProvider, useService };
