interface User {
  first_name: string;
  last_name: string;
  email: string;
  phone: string;
  id: string;
  my_referral_code: string;
  indicated_by_id: string | null;
  is_active: boolean;
  created_at: string;
  updated_at: string;
  is_active_email: boolean;
  is_active_phone: boolean;
  cpf?: string | null;
  cnpj?: string | null;
  username: string;
  photo?: string;
}

const masks = [
  {
    label: "BR +55",
    value: "99 99999-9999",
  },
  {
    label: "US +1",
    value: "999 999-9999",
  },
];

interface Props {
  navigation: any;
  route: any;
}

export const UpdatePetScreen: React.FC<Props> = ({ navigation, route }) => {
  const [pet, setPet] = useState<Pet>();
  const [loading, setLoading] = useState(false);
  const [name, setName] = useState("");
  const [color, setColor] = useState<string>();
  const [colorSelect, setColorSelect] = useState<string>();
  const [species, setSpecies] = useState<Specie[]>([]);
  const { logOut, getUser } = useAuth();
  const [refreshing, setRefreshing] = React.useState(false);
  const [sex, setSex] = useState("");
  const [date, setDate] = useState<Date>();
  const [specie, setSpecie] = useState<string>();
  const [breed, setBreed] = useState<string>();
  const [breeds, setBreeds] = useState<Breed[]>([]);
  function getBreeds(specieId: string) {
    api
      .get(`v1/pets/get/breeds?specie=${specieId}`)
      .then((response) => {
        const breedsr = response.data;
        breedsr.unshift({ id: "", name: "Qual a raça do seu pet" });
        setBreeds(breedsr);
      })
      .catch((error) => {
        console.log(error.response.data);
        if (error.response.status === 401) {
          logOut();
        }
      })
      .finally(() => {
      });
  }
  function getSpecies() {
    setRefreshing(true);
    api
      .get("v1/pets/get/species")
      .then((response) => {
        setSpecies(response.data);

        updatePet(route.params.pet);
      })
      .catch((error) => {
        console.log(error.response.data);
        if (error.response.status === 401) {
          logOut();
        }
      })
      .finally(() => {
        setRefreshing(false);
      });
  }

  function Onrefresh() {
    console.log("refresh");
    getSpecies();
  }

  function sendEditForm() {
    setLoading(true);
    api
      .patch(`v1/pets/${pet?.id}`, {
        name,
        sex,
        color: colorSelect === "Outra" ? color : colorSelect,
        birth_date: moment(date).format("YYYY-MM-DD"),
        breed_id: breed,
      })
      .then((response) => {
        console.log(response.data);
        navigation.goBack();
      })
      .catch((error) => {
        console.log(error.response.data);
        if (error.response.status === 401) {
          logOut();
        }
      })
      .finally(() => {
        setLoading(false);
      });
  }

  function updatePet(petId: string) {
    setRefreshing(true);
    console.log(`v1/pets/${petId}`);
    api
      .get(`v1/pets/${petId}`)
      .then((response) => {
        setPet(response.data);
        setName(response.data.name);
        setColor(response.data.color);
        setSex(response.data.sex);
        setColorSelect(response.data.color);
        setSpecie(response.data.breed.specie_id);
        setDate(new Date(response.data.birth_date));
        setBreed(response.data.breed_id);
        getBreeds(response.data.breed.specie_id);
      })
      .catch((error) => {
        console.log(error.response.data);
        if (error.response.status === 401) {
          logOut();
        }
      })
      .finally(() => {
        setRefreshing(false);
      });
  }

  const [image, setImage] = useState("");

  const pickImage = async () => {
    // No permissions request is necessary for launching the image library
    let result = await ImagePicker.launchImageLibraryAsync({
      mediaTypes: ImagePicker.MediaTypeOptions.All,
      allowsEditing: true,
      aspect: [4, 3],
      quality: 1,
    });
    if (!result.canceled) {
      const data = new FormData();
      if (Platform.OS === "web") {
        const response = await fetch(result.uri);
        const blob = await response.blob();
        data.append(
          "file",
          blob,
          result.uri.split("/")[result.uri.split("/").length - 1]
        );
      } else {
        // data.append("file", {
        //   uri: result.assets[0].uri,
        //   name: result.assets[0].uri.split("/")[
        //     result.assets[0].uri.split("/").length - 1
        //   ],
        //   type: "image/jpeg",
        // });
      }
      setLoading(true);
      api
        .post("/v1/users/edit/photo", data, {
          headers: {
            "Content-Type": "multipart/form-data",
          },
        })
        .then((response) => {
          console.log(response.data);
          getUser();
        })
        .catch((error) => {
          console.log(error.response);
          console.log(error.response.data.detail);
        })
        .finally(() => {
          setLoading(false);
        });
      setImage(result.assets[0].uri);
    }
  };

  useFocusEffect(
    React.useCallback(() => {
      Onrefresh();
    }, [])
  );
  return (
    <LinearGradient
      colors={[theme.palette.littleOrange, theme.palette.white]}
      style={styles.container}
    >
      <TouchableWithoutFeedback onPress={Keyboard.dismiss}>
        <KeyboardAvoidingView
          style={styles.keyboardAvoidingContainer}
          behavior={Platform.OS === "ios" ? "padding" : undefined}
        >
          <View style={styles.header}>
            <TouchableOpacity
              style={styles.buttonBack}
              onPress={() => navigation.goBack()}
            >
              <BackIcon size={10} />
              <Text style={styles.headerTextButton}>Voltar</Text>
            </TouchableOpacity>
            <Text style={styles.headerTitle}>Editar Pet</Text>
            <TouchableOpacity
              onPress={() => sendEditForm()}
            >
              <Text style={styles.headerSuccessButton}>Feito</Text>
            </TouchableOpacity>
          </View>
          <ScrollView
            refreshControl={
              <RefreshControl refreshing={refreshing} onRefresh={Onrefresh} />
            }
          >
            <View style={styles.content}>
              <TouchableOpacity style={styles.photo} onPress={pickImage}>
                {image && (
                  <Image
                    style={styles.photoImage}
                    source={{
                      uri: image,
                    }}
                  />
                )}
                <View style={styles.photoIcon}>
                  <CameraIcon size={24} color={theme.palette.secondaryOrange} />
                </View>
              </TouchableOpacity>
              <View style={styles.fomControl}>
                <Text style={styles.label}></Text>
                <View style={styles.input}>
                  <Select
                    onChange={(value) => {
                      setSpecie(value);
                      getBreeds(value);
                    }}
                    defaultValue={specie}
                    itens={species.map((item) => ({
                      label:
                        item.name === "Cachorro" ? "🐶 Cachorro" : "🐱 Gato",
                      value: item.id,
                    }))}
                  />
                </View>
              </View>
              <View style={styles.fomControl}>
                <Text style={styles.label}>Nome</Text>
                <TextInput
                  keyboardType="default"
                  placeholder="Qual nome do seu pet?"
                  style={styles.input}
                  placeholderTextColor={theme.palette.gray2}
                  onChangeText={(text) => {
                    setName(text);
                  }}
                  value={name}
                  // onLayout={onLayout}
                  // ref={textInputRef}
                />
              </View>
              <View style={styles.fomControl}>
                <Text style={styles.label}>Raça</Text>
                <View style={styles.input}>
                  <Select
                    onChange={(value) => {
                      setBreed(value);
                    }}
                    defaultValue={breed}
                    itens={breeds.map((item) => ({
                      label: item.name,
                      value: item.id,
                    }))}
                  />
                </View>
              </View>

              <View style={styles.fomControl}>
                <Text style={styles.label}>Data de nascimento</Text>
                <View style={styles.input}>
                  <SelectDate
                    label="Quando ele nasceu ?"
                    value={date}
                    onChange={(date) => setDate(date)}
                  />
                </View>
              </View>
              <View style={styles.fomControl}>
                <Text style={styles.label}>Sexo</Text>
                <View style={styles.input}>
                  <Select
                    onChange={(value) => {
                      setSex(value);
                    }}
                    defaultValue={sex}
                    itens={[
                      { label: "Qual o sexo do seu pet ?", value: "" },
                      { label: "Macho", value: "Macho" },
                      { label: "Fêmea", value: "Fêmea" },
                    ]}
                  />
                </View>
              </View>
              <View style={styles.fomControl}>
                <Text style={styles.label}>Cor</Text>
                <View style={styles.input}>
                  <Select
                    onChange={(value) => {
                      setColorSelect(value);
                    }}
                    defaultValue={colorSelect}
                    itens={[
                      { label: "Qual a cor  do seu pet ?", value: "" },
                      { label: "Preto", value: "Preto" },
                      { label: "Branco", value: "Branco" },
                      { label: "Marrom", value: "Marrom" },
                      { label: "Caramelo", value: "Caramelo" },
                      { label: "Dourado", value: "Dourado" },
                      { label: "Cinza", value: "Cinza" },
                      { label: "Preto e branco", value: "Preto e branco" },
                      { label: "Marrom e branco", value: "Marrom e branco" },
                      { label: "Cinza e branco", value: "Cinza e branco" },
                      { label: "Creme", value: "Creme" },
                      { label: "Fulvo", value: "Fulvo" },
                      { label: "Tigrado", value: "Tigrado" },
                      { label: "Azul", value: "Azul" },
                      { label: "Ruão", value: "Ruão" },
                      { label: "Champanhe", value: "Champanhe" },
                      { label: "Chocolate", value: "Chocolate" },
                      { label: "Outra", value: "Outra" },
                    ]}
                  />
                </View>
                {colorSelect === "Outra" && (
                  <TextInput
                    placeholder="Qual cor?"
                    style={styles.input}
                    placeholderTextColor={theme.palette.gray2}
                    onChangeText={(text) => {
                      setColor(text);
                    }}
                    value={color}
                  />
                )}
              </View>
            </View>
          </ScrollView>
          <LoadingPage visible={loading} />
        </KeyboardAvoidingView>
      </TouchableWithoutFeedback>
    </LinearGradient>
  );
};

